<template>
  <div id="app">
    <h2 class="page-title" v-if="title" @click="backIndex">{{title}}</h2>
    <router-view></router-view>
  </div>
</template>

<script>

export default {
  name: 'app',
  components: {
  },
  data() {
    return {
      title: ''
    }
  },
  watch: {
    $route() {
      this.initTitle();
    }
  },
  methods: {
    initTitle() {
      let name = this.$route.name;
      let nameArr = this.$router.options.routes;
      this.title = '';
      nameArr.forEach(ele => {
        if (ele.name === name && ele.data) {
          this.title = ele.data.title;
        }
      })
    },
    backIndex() {
      this.$router.push('/#/');
    }
  },
  created() {
    this.initTitle();
  }
}
</script>

