<template>
  <div class="stream-text-wrapper">
    <div class="menu-stream">
      <div :class="['menu-text', {'active': menuId === 0}]" @click="menuId=0">首行缩进</div>
      <div :class="['menu-text', {'active': menuId === 1}]" @click="menuId=1">毛边处理</div>
    </div>
    <div class="menu-content" v-if="menuId===0">
      <div class="indent-menus pull-left">
        <div class="indext-menu-item item-title">空格标记</div>
        <div class="indext-menu-item" v-for="(item, index) in indentSpace" :key="index" @click="addDisc(item.disc)">{{item.name}}</div>
        <div class="indext-menu-item item-title" @click="addDisc(cssDisc)">css样式</div>
      </div>
      <div class="indent-discribe" v-if="discPart.length > 1">
        <div class="indent-disc-content" v-for="(item, index) in discPart" :key="index" >{{item}}</div>
      </div>
      <div class="indent-discribe" v-else>
        <a class="indent-disc-content" target="blank" href="https://www.w3school.com.cn/tiy/t.asp?f=csse_text-indent">{{discPart[0]}}</a>
      </div>
    </div>
    <div class="menu-content" v-if="menuId===1">
      <div class="justify-content">
        <p class="justify-method" :style="methodCss">样式排版中我们所谓的毛边处理就是让文字做到左右对齐，我们可以用到一个属性<span class="addCss" @click="addCss('text-align', 'justify')">text-align: justify;</span>,但是这个属性有部分问题，当你只有一行或者在最后一行他还是默认向左对齐。针对这一现象我们也有解决方案，第一个，增加属性<span class="addCss" @click="addCss('text-align-last', 'justify')">text-align-last：justify;</span>其意义在于告诉浏览器在最后一行也要进行两端对齐，但是兼容性较差。那么如果我们看到的最后一行实际上并不是最后一行那也可以做到两端对齐，那么就在需要对齐的盒子中增加一行影藏的内容，并且为了兼容ie浏览器在外盒子中增加属性<span class="addCss" @click="addCss('text-justify', 'inter-ideograph')">text-justify:inter-ideograph;</span>对样式进行调整。</p>
        <p class="justify-method" :style="methodCss">但是，你pc端兼容没问题了，安卓手机自带浏览器又来了，它不支持text-justify，唯一的解决方法，你在以上代码的基础上，对你要左右对齐的所有内容每个字之间手动加空格。</p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
export default {
  components: {
  },
  name: 'streamText',
  data() {
    return {
      menuId: -1,
      indentSpace: [
        {
          name:'&nbsp;',
          disc: ['&nbsp;称之为断行空格', '相当于按下空格键所产生的空格的宽度,该空格占据宽度受字体影响明显']
        },
        {
          name:'&ensp;',
          disc: ['&ensp;也叫半角空格', '其占据的宽度正好是1/2个中文宽度，而且基本上不受字体影响']
        },
        {
          name:'&emsp;',
          disc: ['&emsp;是全角空格', '占据的宽度正好是1个中文宽度。所以我们在使用首行缩进的时候最适合的是用&emsp;进行处理。使断落更加美观。']
        },
        {
          name:'&thinsp;',
          disc: ['还有三种空格在我们的使用过程中并不是很常见','一个是&thinsp;他占据了em六分之一的宽度','&zwnj;在页面中主要是用来在两个字符之间抑制本来会发生的连字','&zwj;则是使两个本不会发生连字的字符产生连字的效果，多用于阿拉伯语等语种。']
        },
        {
          name:'&zwnj;',
          disc: ['还有三种空格在我们的使用过程中并不是很常见','一个是&thinsp;他占据了em六分之一的宽度','&zwnj;在页面中主要是用来在两个字符之间抑制本来会发生的连字','&zwj;则是使两个本不会发生连字的字符产生连字的效果，多用于阿拉伯语等语种。']
        },
        {
          name:'&zwj;',
          disc: ['还有三种空格在我们的使用过程中并不是很常见','一个是&thinsp;他占据了em六分之一的宽度','&zwnj;在页面中主要是用来在两个字符之间抑制本来会发生的连字','&zwj;则是使两个本不会发生连字的字符产生连字的效果，多用于阿拉伯语等语种。']
        }
      ],
      discPart: [],
      cssDisc: ['text-indent: 2em;'],
      methodCss: []
    }
  },
  methods: {
    ...mapActions([
    ]),
    addDisc(disc) {
      this.discPart = disc;
    },
    addCss(name, value) {
      let obj = new Object();
      obj[name] = value;
      this.methodCss.push(obj)
    }
  },
  watch: {
  },
  computed: {
    ...mapGetters([
    ])
  },
  created() {
  },
  mounted() {
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import '../assets/scss/common.scss';
.stream-text-wrapper {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding-top: 150px;
  .menu-stream {
    display: flex;
    width: 100%;
    height: 30px;
    justify-content: center;
  }
  .menu-text {
    padding-left: 20px;
    padding-right: 20px;
    font-size: 24px;
    line-height: 30px;
    color: #00a4ff;
    cursor: pointer;
    &.active {
      text-decoration: underline;
    }
  }
  .menu-content {
    flex: 1;
    width: 100%;
  }
  .indent-menus {
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    width: 30%;
    height: 100%;
  }
  .indext-menu-item {
    width: 100%;
    height: 50px;
    text-align: right;
    line-height: 50px;
    font-size: 22px;
    cursor: pointer;
    &:first-of-type {
      cursor: default;
    }
    &.item-title {
      color: #003cff;
      font-size: 24px;
    }
    &:hover:not(.item-title) {
      color: #00a4ff;
    }
  }
  .indent-discribe {
    display: flex;
    align-content: center;
    flex-wrap: wrap;
    height: 100%;
    padding-left: 100px;
    padding-right: 300px;
  }
  .indent-disc-content {
    display: block;
    font-size: 24px;
    text-align: left;
    text-indent: 2em;
  }
  .justify-content {
    width: 100%;
    height: 100%;
    padding-left: 300px;
    padding-right: 300px;
    text-align: left;
    font-size: 22px;
  }
  .justify-method {
    text-indent: 2em;
  }
  .addCss {
    color: #00a4ff;
    cursor: pointer;
  }
}
</style>