<template>
  <div class="index-wrapper">
    <div class="index-banner">
      <div class="index-css">
        <div class="banner-css-word banner-css-c">
          <div class="banner-line-f"></div>
          <div class="banner-line-s"></div>
          <div class="banner-line-t"></div>
        </div>
        <div class="banner-css-word banner-css-s">
          <div class="banner-line-f"></div>
