    }
  },
  watch: {
  },
  computed: {
    ...mapGetters([
    ])
  },
  created() {
  },
  mounted() {
    this.initCanvas();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../assets/scss/common.scss";
.index-wrapper {
  width: 100%;
  height: 100%;
  .index-banner {
    width: 100%;
    height: 60%;
    padding-top: 100px;
    .index-css {
      display: flex;
      justify-content: space-between;
      width: 220px;
      height: 70px;
      margin: 0 auto 30px;
    }
    .banner-css-word {
      width: 55px;
      height: 100%;
      overflow: hidden;
    }
    .banner-css-c {
      position: relative;
      .banner-line-f {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 22px;
        background-color: #000;
      }
      .banner-line-s {
        position: absolute;
        left: 0;
        top: 0;
        width: 24px;
        height: 100%;
        background-color: #000;
      }
      .banner-line-t {
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 22px;
        background-color: #000;
      }
    }
    .banner-css-s {
      position: relative;
      .banner-line-f {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 22px;
        background-color: #000;
      }
      .banner-line-s {
        position: absolute;
        left: 50%;
        top: 0;
        width: 24px;
        height: 100%;
        transform: translateX(-50%) rotate(-42deg);
        background-color: #000;
      }
      .banner-line-t {
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 22px;
        background-color: #000;
      }
    }
    .index-banner-inner {
      width: 100%;
      height: calc(100% - 100px);
      background: url("../../public/images/banner.png") no-repeat center;
      background-clip: content-box;
      background-size: contain;
    }
  }
  .index-title {
    position: relative;
    font-size: 50px;
    text-align: center;
    text-shadow: 4px 4px 1px #333;
    overflow: hidden;
    &::before {
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-image: linear-gradient(
        -45deg,
        aliceblue 0%,
        aliceblue 25%,
        transparent 25%,
        transparent 50%,
        aliceblue 50%,
        aliceblue 75%,
        transparent 75%,
        transparent 100%
      );
      background-size: 6px 6px;
      z-index: 1;
    }
    &::after {
      position: absolute;
      content: attr(data-name);
      top: -4px;
      left: -2px;
      right: 6px;
      bottom: 6px;
      color: #333;
      z-index: 2;
      text-shadow: 3px 3px aliceblue;
    }
  }
  #index-canvas {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 3;
    width: 100%;
    height: 100%;
    pointer-events: none;
  }
  .menu-box {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    .menu-ball {
      position: absolute;
      left: 0;
      top: 0;
      width: 150px;
      height: 150px;
      padding: 40px 20px;
      font-size: 26px;
      line-height: 35px;
      text-align: center;
      border-radius: 50%;
      color: #0099ff;
      box-shadow: 0 0 20px 1px #00a4ff inset;
      &::before,
      &::after {
        display: none;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        box-shadow: 0 0 20px 1px rgba(0, 166, 255, 0.2), 0 0 20px 1px rgba(0, 166, 255, 0.2) inset;
        animation: move 3s linear infinite;
        content: '';
      }
      &::before {
        animation-delay: 1.5s;
      }
      @keyframes move {
        0% {
          opacity: 1;
          transform: scale(1);
        }
        100% {
          opacity: 0;
          transform: scale(2);
        }
      }
      &:hover {
        &::before,
        &:after {
          display: block;
        }
      }
      &:nth-of-type(1) {
        left: 20%;
        top: 10%;
      }
      &:nth-of-type(2) {
        padding-top: 55px;
        width: 180px;
        height: 180px;
        left: 10%;
        top: 75%;
      }
      &:nth-of-type(3) {
        padding-top: 50px;
        width: 170px;
        height: 170px;
        left: 25%;
        top: 35%;
      }
      &:nth-of-type(4) {
        left: 80%;
        top: 40%;
      }
      &:nth-of-type(5) {
        padding-top: 50px;
        width: 180px;
        height: 180px;
        left: 60%;
        top: 20%;
      }
      &:nth-of-type(6) {
        padding-top: 30px;
        width: 120px;
        height: 120px;
        left: 50%;
        top: 75%;
      }
    }
  }
}
</style>