<template>
  <div class="dash-line-wrapper">
    <div class="next-box" @click="next">next</div>
    <div class="content-wrapper" v-if="step === 1">
      <div class="back-image-line"></div>
      <p>width: 100%;</p>
      <p>height: 3px;</p>
      <p>background-image: linear-gradient(to right, #bbb 0%, #bbb 30%, transparent 30%, transparent 50%,#000 50%, #000 60%, transparent 60%,transparent 100%);</p>
      <p>background-size: 20px 100%;</p>
      <div class="shadow-line"></div>
      <p>position: relative;</p>
      <p>width: 100%;</p>
      <p>&::after {</p>
      <p class="indent-after">position: absolute;</p>
      <p class="indent-after">left: 0;</p>
      <p class="indent-after">top: 0;</p>
      <p class="indent-after">width: 50px;</p>
      <p class="indent-after">height: 1px;</p>
      <p class="indent-after">transform: translateX(-50px);</p>
      <p class="indent-after">box-shadow: 50px 0 #bbb, 150px 0 #bbb, 250px 0 #bbb;</p>
      <p class="indent-after">content: '';</p>
      <p>}</p>
    </div>
    <div class="content-wrapper" v-else>
      <div class="shadow"></div>
      <div v-if="step === 3">
        <div class="cssload-box cssload-loading"></div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  components: {},
  name: "dashLine",
  data() {
    return {
      step: 1
    };
  },
  methods: {
    ...mapActions([]),
    next() {
      this.step++;
      if (this.step > 3) {
        this.$router.push("/#/");
      }
    }
  },
  watch: {},
  computed: {
    ...mapGetters([])
  },
  created() {
    this.step = 1;
  },
  mounted() {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../assets/scss/common.scss";
